        transform: translateY(calc(14.48vh + 160px));
    }
}

/*--------------------------------------------------------------------*/
/*------------------------------ BUTTONS -----------------------------*/
/*--------------------------------------------------------------------*/

.E_PageMenu--button {
    z-index: 2;
    position: absolute;
    top: 0px;
    left: 0px;

    width: 64px;
    height: 64px;

    visibility: visible;
    opacity: 1;

    transition:
        visibility 0s   linear 0.6s,
        opacity    0.3s linear 0.6s;

    cursor: default;
    touch-action: manipulation;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
