    }

    .E_PageSectionSettings__multiple-tabs .E_PageSectionSettings--scrollable:last-of-type  > .E_PageSectionSettings--scrolling-content {
        min-width: calc(100vw - 40px - 1px);
    }
}

/*---------------------------- CLOSE | MINMAX ----------------------------*/

.E_PageSectionSettings--min-max,
.E_PageSectionSettings--close {
    z-index: 1100;
    position: absolute;
    top: 0px;
    right: 0px;

    width: 24px;
    height: 24px;
    padding-top: 2px;

    display: flex;
    align-items: center;
    justify-content: center;

    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 1));
    -moz-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 1));
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 1));

    transition: var(--default-transition);
}

