    padding-right: 2px;
}

.E_PageSectionSettings--min-max {
    padding-left: 4px;
}

.E_PageSectionSettings--min-max svg,
.E_PageSectionSettings--close svg {
    width: 12px;
    height: 12px;

    fill: #404040;
    opacity: 0.4;

    transition: var(--default-transition);
}

.E_PageSectionSettings--min-max {
    right: 24px;
}

@media (pointer: coarse) {

    .E_PageSectionSettings--min-max,
    .E_PageSectionSettings--close {
        width: 30px;
        height: 30px;
        padding-top: 0;
    }

    .E_PageSectionSettings--close {
        padding-right: 2px;
    }

    .E_PageSectionSettings--min-max {
        padding-left: 8px;
    }

    .E_PageSectionSettings--min-max svg,
    .E_PageSectionSettings--close svg {
        width: 14px;
        height: 14px;
    }

    .E_PageSectionSettings--min-max {
        right: 30px;
    }
}

.E_PageSectionSettings--min-max__max {
    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    -moz-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
}

.E_PageSectionSettings--min-max__max svg {
    fill: var(--color-selected);
    opacity: 1;
}

.E_PageSectionSettings--min-max:hover,
.E_PageSectionSettings--close:hover {
    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    -moz-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));

    transition: var(--default-transition-hover);
}

.E_PageSectionSettings--min-max:hover svg,
.E_PageSectionSettings--close:hover svg {
    opacity: 1;

    transition: var(--default-transition-hover);
}

.E_PageSectionSettings--min-max:active svg,
.E_PageSectionSettings--close:active svg {
    transform: scale(0.9);

    transition: var(--default-transition-active);
}

/*---------------------------- RESIZER ----------------------------*/

.E_PageSectionSettings--resizer {
    z-index: 100;
    position: absolute;
    bottom: -7px;
    left: 0px;

    width: 100%;
    height: 12px;

    cursor: ns-resize;
}

@media (pointer: coarse) {

    .E_PageSectionSettings--resizer {
        bottom: -13px;

        height: 24px;
    }
}

.E_PageSectionSettings--resizer:after {
    content: "";

    position: absolute;
    top: calc(50% - 1px);
    left: 0px;

    width: 100%;
    height: 0px;

    border-bottom: 1px solid transparent;

    cursor: ns-resize;

    transition: var(--default-transition);

    transform: translateZ(0);
}

.E_PageSectionSettings--resizer:hover:after {
    border-bottom-color: var(--color-selected);

    transition: var(--default-transition-hover);
}

